    width: 100%;
    height: auto;
    margin:0 auto;
    background: #EEE;
}
.grid-sizer,
.grid-item {
    width:32%;
    margin:0.3%;
}
.grid-item{
    float: left;
}
.grid-item img{
    width: 100%;
    display: block;
}
.grid::after {
    content: '';
    display: block;
    clear: both;
}

/* notyf */
.notyf__toast {
    background-color: rgba(42, 255, 237, 0.475) !important;
}
.img-header {
    width:100%;
    
}
.begin-img {
    width:100%;
}
